import axios from 'axios';

//   axios
const API_KEY = 'b5dbc40d665affe8ed0bac71106b3fa8';
axios.defaults.baseURL = 'https://api.themoviedb.org/3';

export default class MovieDB {
  #searchQuery = '';
  #page = 1;

  //  
  #moviesParams = {
    params: {
      api_key: API_KEY,
    },
  };

  //      
  async fetchPopularMovie(lang) {
    const url = `trending/movie/day?page=${this.#page}&language=${lang}`;

    const { data } = await axios.get(url, this.#moviesParams);

    return data;
  }

  //       ()
  async fetchSearchMovie(lang) {
    const url = `search/movie/?query=${this.#searchQuery}&page=${
      this.#page
    }&language=${lang}`;

    const { data } = await axios.get(url, this.#moviesParams);

    return data;
  }

  //     
  async fetchMoviesGenres(lang) {
    const {
      data: { genres },
    } = await axios.get(
      `genre/movie/list?language=${lang}`,
      this.#moviesParams
    );

    return genres;
  }

  //      
  async fetchMoviesDiscoverByGenres(id, lang) {
    const { data } = await axios.get(
      `discover/movie?with_genres=${id}&page=${this.#page}&language=${lang}`,
      this.#moviesParams
    );

    return data;
  }

  //      
  async fetchMoviesDiscoverByYear(year, lang) {
    const { data } = await axios.get(
      `discover/movie?primary_release_year=${year}&page=${
        this.#page
      }&language=${lang}`,
      this.#moviesParams
    );

    return data;
  }

  //          id
  async fetchMovieDetails(id, lang) {
    const url = `movie/${id}?language=${lang}`;

    const { data } = await axios.get(url, this.#moviesParams);

    return data;
  }

  //       id
  async fetchMovieTrailer(id) {
    const url = `movie/${id}/videos?`;

    const { data } = await axios.get(url, this.#moviesParams);

    return data;
  }

  get query() {
    return this.#searchQuery;
  }

  set query(newQuery) {
    this.#searchQuery = newQuery;
  }

  get page() {
    return this.#page;
  }

  set page(newPage) {
    this.#page = newPage;
  }
}
